import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			orderList: [],
			money: 0, //消费金额
			sheng: 0, //升
			current: 0,
			list: [{
					title: "全部",
				},
				{
					title: "未支付",
				},
				{
					title: "已支付",
				}
			]
		}
	},
	computed: mapState(['serverUrl']),
	onLoad() {
		uni.showLoading({
			title: '加载中'
		});
		this.current = 0;
		this.getOrderList();
		setTimeout(() => {
			uni.hideLoading()
		}, 1000)
	},
	onShow() {
		this.current=0;
		this.getOrderList();
	},
	methods: {
		// 订单详情
		toOrderInfo(id) {
			uni.navigateTo({
				url: `/pages/tabs/cart/orderInfo?id=${id}`
			})
		},

		tap(index) {
			this.current = index;
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(() => {
				uni.hideLoading()
			}, 1000)
			if(index != 0){
			console.log(index)
			this.getList(this.current);
			}else if(index == 0){
				this.getOrderList();
			}
		},
		stap(e) {
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(() => {
				uni.hideLoading()
			}, 1000)
				this.current = e.detail.current;
			if(e.detail.current != 0 ){
				console.log(e.detail.current)
				this.getList(this.current);
			}else if(e.detail.current == 0){
				this.getOrderList();
			}
			
		},
		//订单列表
		getOrderList() {
			this.money = 0;
			this.sheng = 0;
			uni.request({
				url: this.serverUrl + '/api/wx/filling/order/list', //仅为示例，并非真实接口地址。
				header: {
					'token': uni.getStorageSync('token'),
					'Cookie': uni.getStorageSync('setcookie')
				},
				success: (res) => {
					console.log("全部订单列表", res)
					this.orderList = res.data.data.orders;
					this.sheng = res.data.data.litre;
					this.money = res.data.data.total;
				}
			});
		},
		// 
		getList(status) {
			uni.request({
				url: this.serverUrl + `/api/wx/filling/order/paystatus/get/${status}`, //仅为示例，并非真实接口地址。
				header: {
					'token': uni.getStorageSync('token'),
					'Cookie': uni.getStorageSync('setcookie')
				},
				success: (res) => {
					console.log("支付列表", res)
					this.orderList = res.data.data;
				}
			});
		},
	}
}
